/**
 * Retrieves the translation of text.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-i18n/
 */
import { __ } from '@wordpress/i18n';
import { RawHTML } from '@wordpress/element';

/**
 * React hook that is used to mark the block wrapper element.
 * It provides all the necessary props like the class name.
 *
 * @see https://developer.wordpress.org/block-editor/packages/packages-block-editor/#useBlockProps
 */
import { useBlockProps } from '@wordpress/block-editor';

/**
 * The save function defines the way in which the different attributes should
 * be combined into the final markup, which is then serialized by the block
 * editor into `post_content`.
 *
 * @see https://developer.wordpress.org/block-editor/developers/block-api/block-edit-save/#save
 *
 * @return {WPElement} Element to render.
 */
export default function save(props) {
     const { attributes } = props;

	return (
        <div className="oese-tabs-block" { ...useBlockProps.save() }>
    		<div className={ 'oese-tabs-block' }>
                <ul className="nav nav-tabs" id="oeseTabs" role="tablist">
                    {attributes.tab1Checked && <li className="nav-item">
                        <a className="nav-link active" id={ 'Tab' + attributes.blockId + '_1' } data-toggle="tab" href={ '#TabContent' + attributes.blockId + '_1' } role="tab" aria-controls="home" aria-selected="true">
                            <i className={ 'fas ' + attributes.tab1Icon }></i> {attributes.tab1Label}
                        </a>
                    </li>}
                    {attributes.tab2Checked && <li className="nav-item">
                        <a className="nav-link" id={ 'Tab' + attributes.blockId + '_2' } data-toggle="tab" href={ '#TabContent' + attributes.blockId + '_2' } role="tab" aria-controls="profile" aria-selected="false">
                            <i className={ 'fas ' + attributes.tab2Icon }></i> {attributes.tab2Label}
                        </a>
                    </li>}
                    {attributes.tab3Checked && <li className="nav-item">
                        <a className="nav-link" id={ 'Tab' + attributes.blockId + '_3' } data-toggle="tab" href={ '#TabContent' + attributes.blockId + '_3' } role="tab" aria-controls="contact" aria-selected="false">
                            <i className={ 'fas ' + attributes.tab3Icon }></i> {attributes.tab3Label}
                        </a>
                    </li>}
                </ul>
                <div className="tab-content" id={ 'TabContent' + attributes.blockId }>
                    {attributes.tab1Checked && <div className="tab-pane fade show active" id={ 'TabContent' + attributes.blockId + '_1' } role="tabpanel" aria-labelledby={ 'Tab' + attributes.blockId + '_1' }>
                        <RawHTML>{attributes.tab1Content}</RawHTML>
                    </div>}
                    {attributes.tab2Checked && <div className="tab-pane fade" id={ 'TabContent' + attributes.blockId + '_2' } role="tabpanel" aria-labelledby={ 'Tab' + attributes.blockId + '_2' }>
                        <RawHTML>{attributes.tab2Content}</RawHTML>
                    </div>}
                    {attributes.tab3Checked && <div className="tab-pane fade" id={ 'TabContent' + attributes.blockId + '_3' } role="tabpanel" aria-labelledby={ 'Tab' + attributes.blockId + '_3' }>
                        <RawHTML>{attributes.tab3Content}</RawHTML>
                    </div>}
                </div>
            </div>
        </div>
	);
}
